import React, { Suspense, useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import { Button, Card, CardBody, CardHeader, CardTitle, Table } from "react-bootstrap";
import SeeMoreModal from "../../layout/common/SeeMoreModal"; 
import ConnectButton from "../../layout/common/connectButton";
import AuthorInfo from "./authorInfo";
import PostMedia from "../../layout/common/postMedia";
import NoData from "../../layout/common/NoData";
import { getwallRecordMostActiveUsersMore } from "../../../../../actions/analyticsNew";
const LeaderbordSkeleton = React.lazy(() =>
  import("../../layout/skeleton/LeaderbordSkeleton")
);

const LeaderbordPopup = React.lazy(() =>
  import("../../layout/popup/LeaderbordPopup")
);

const LeaderBoard = (props) => {

    const settings = {
      type: "slide",
      rewind: false,
      speed: 1000,
      loop: true,
      interval: 7000,
      drag: true,
      perPage: 4,
      gap: 5,
      autoplay: false,
      padding: "0",
      pagination: false,
      arrows: true,
  };

  const { leaderBoard, loader } = props;

  const [show, setShow] = useState(false)

  const handlePopup = event => {  
    setShow(event)
  }

  return (
   <>
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <CardHeader className="px-5 pt-5 border-0 pb-0">
        <CardTitle className="mb-0 d-flex align-items-center justify-content-between">
          <h6 class="card-label font-weight-bolder m-0">Leader Board</h6>
          { leaderBoard.total>5 ?  <Button variant="link" className="p-0" onClick={()=>handlePopup(true)}>See More</Button> : null }
        </CardTitle>
      </CardHeader>
      <CardBody className="py-5 px-0">
        <Table responsive>
          <thead>
            <tr>
              <th style={{ width: "10%" }}>
                S No.
              </th>
              <th style={{ width: "30%" }}>
                Name
              </th>
              <th style={{ width: "15%" }}>
                No. Followres
              </th>
              <th style={{ width: "15%" }}>
                No. of Posts
              </th>
              <th style={{ width: "20%" }}>
                Recent Posts
              </th>
              <th style={{ width: "10%" }}></th>
            </tr>
          </thead>
          <tbody>
            

            { loader ? <Suspense fallback={<></>}> <LeaderbordSkeleton/> </Suspense> : leaderBoard && Object.keys(leaderBoard.data).length
              ? leaderBoard.data.map((data,index) => {
                  return (
                    <tr className="border-top">
                      <td>
                        <strong>{index+1}.</strong>
                      </td>
                      <td> { console.log(data,'datadfsdfsdfsfd')}
                        <AuthorInfo picture={data.image} username={data.author.name} isIcon={data.network.icon} />
                      </td>
                      <td>
                        <strong>{data.author.follower}</strong>
                      </td>
                      <td>
                        <strong>{data.postCount}</strong>
                      </td>
                      <td>
                        
                      { data.postsData && Object.keys(data.postsData).length && data.postsData.filter(obj => obj.postFile!="").length ?                                 
                            <Splide
                            className="recent-post m-0"
                            hasTrack={false}
                            options={settings}
                            >
                            <SplideTrack>
                              { data.postsData.map((asset, index) => {
                                return (
                                    <SplideSlide key={index}>
                                       <PostMedia postFile={asset.postFile} postId={asset.postId} className={'img-fluid'} />
                                    </SplideSlide>
                                );
                                })}
                            </SplideTrack>
                            </Splide> : null }
                      </td>
                      <td>
                        <ConnectButton link={data.links}/>
                      </td>
                    </tr>
                  );
                })
              : 
              <>
              <NoData status={1} col={5}/>
              </>
             }

          </tbody>
        </Table>
      </CardBody>
     </Card>

   {leaderBoard.total>5 ? 
    <SeeMoreModal size={'lg'} showHide={show} handlePopup={handlePopup} title={'Leader Bord'}>
      <Suspense fallback={<></>}>
         <LeaderbordPopup moreData={getwallRecordMostActiveUsersMore} {...props} />
       </Suspense>  
    </SeeMoreModal> : null }
    </>    
  );
};

export default React.memo(LeaderBoard);
